@font-face {
  font-family: "yekan";
  src: url(../fonts/Yekan/BYekan.eot);
  src: url(../fonts/Yekan/BYekan.woff) format("woff"), url(../fonts/Yekan/BYekan.eot?#iefix) format("embedded-opentype"), url(../fonts/Yekan/BYekan.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
}
::selection {
  color: #000;
  background: #F4D313;
}
.arial {
  font-family: "Helvetica Neue", Helvetica, Verdana, Arial, sans-serif;
}
.yekan {
  font-family: "yekan", Tahoma, Verdana;
}
.edit {
  font-family: Tahoma, "Helvetica Neue", Helvetica, Verdana, Arial, sans-serif;
  direction: ltr;
  text-align: left;
  float: left;
}
body {
  background: #415B76;
  margin-top: 30px;
  font-family: "yekan", Tahoma, Verdana;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "yekan", Tahoma, Verdana;
  color: #415B76;
  text-shadow: 1px 1px 7px #7D7D7D;
}
code {
  display: inline-block;
  direction: ltr;
  text-align: left;
  font-size: 80%;
}
pre {
  direction: ltr;
  float: left;
  text-align: left;
  overflow: auto;
  width: 100%;
  line-height: 1;
  display: block;
  margin: 0 0 11px;
  padding: 10.5px;
  font-size: 13px;
  word-break: break-all;
  word-wrap: break-word;
  border: 1px solid #CCC;
  border-radius: 4px;
  background-color: #F5F5F5;
}
p {
  font-family: "yekan", Tahoma, Verdana;
  font-size: 14px;
  line-height: 1.4em;
  color: #303030;
  padding: 0 2%;
}
ol > li > ul > li {
  margin-right: 20px;
}
ol {
  margin-right: 20px;
}
aside h2 {
  font-size: 1.8em;
  color: #E74C3C;
  border-bottom: 1px dotted #415B76;
}
textarea {
  resize: vertical;
  padding: 15px !important;
}
legend {
  font-family: "yekan", Tahoma, Verdana;
}
hr {
  margin: 22px 0;
  border: 1px solid #415B76;
}
@media (max-width: 767px) {
  dd {
    float: left;
  }
}
@media (max-width: 767px) {
  .dl-horizontal dd {
    margin-left: 0;
  }
}
.lead {
  margin-top: 15px;
  margin-bottom: 5px;
}
.list-group-item-text {
  margin-right: 20px;
}
.list-group-item li {
  margin-right: 20px;
}
a:hover,
a:focus {
  color: #E74C3C;
  text-decoration: none;
  transition: color 0.5s ease;
}
.container {
  background: #F5F5DC;
  -webkit-border-radius: 0 60px 0 0;
  -moz-border-radius: 0 60px 0 0;
  border-radius: 0 60px 0 0;
}
@media (max-width: 767px) {
  .container {
    -webkit-border-radius: 0 30px 0 0;
    -moz-border-radius: 0 30px 0 0;
    border-radius: 0 30px 0 0;
  }
}
.main {
  background: #F5F5DC;
  min-height: 400px;
  -webkit-animation: fadeIn 2s;
  -moz-animation: fadeIn 2s;
  -o-animation: fadeIn 2s;
  animation: fadeIn 2s;
}
.navbar {
  margin-bottom: 0;
  border-radius: 0;
}
.navbar-toggle {
  position: absolute;
  top: 2px;
  right: 2px;
  padding: 5px 10px;
  background-color: #415B76;
  border: 1px solid #DDD;
  border-radius: 4px;
}
.navbar-toggle:hover,
.navbar-toggle:focus {
  background-color: #F5F5DC;
}
.carousel {
  margin-bottom: 0;
}
.carousel-caption > * {
  color: #00CDA0;
}
.main > article {
  margin: 5% 5%;
}
.sidebar > aside {
  padding: 5%;
}
@media (max-width: 767px) {
  .sidebar > aside {
    padding: 5%;
  }
}
#branding img {
  margin: 20px 25px 5px 0;
  max-width: 450px;
}
@media (max-width: 767px) {
  #branding img {
    margin: 20px 10px 5px 0;
    width: 90%;
  }
}
a.visited:visited {
  color: transparent;
}
.accordion-heading {
  background: #415B76;
  border-radius: 5px;
}
.accordion-heading a {
  color: white;
}
.about article {
  margin: 0;
  padding: 0;
}
.about h2 {
  padding: 0;
  border-bottom: 1px dotted #415B76;
}
.about h3 {
  color: #E74C3C;
  padding: 0;
  margin: 5px;
  font-size: 1.5em;
}
.nav-tabs li a {
  color: #415B76;
}
.nav-tabs li.active a {
  background: #F5F5DC;
}
.nav-tabs li a:hover {
  background: #E74C3C;
  color: white;
}
.nav-tabs li.active a {
  color: #E74C3C;
  font-weight: bold;
}
.tab-content h3,
.tab-content h4 {
  margin: 0;
  padding: 0;
}
.tab-content h3 {
  color: #E74C3C;
  line-height: 1em;
}
.tab-content p {
  padding: 0;
  padding-top: 10px;
}
.photogrid img {
  width: 96px;
  margin: 0 5px 5px 0;
  cursor: pointer;
}
@media (max-width: 990px) {
  .photogrid img {
    width: 64px;
  }
}
ul.breadcrumb a:hover {
  color: #E74C3C;
}
footer ul.breadcrumb {
  background: #415B76;
  margin: 0 auto;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  color: white;
  font-size: 13px;
}
footer ul.breadcrumb a > i:hover {
  color: #F4D313;
  transition-duration: 1s;
}
footer ul.breadcrumb i {
  color: white;
}
footer ul.breadcrumb li {
  text-shadow: none;
}
footer ul.breadcrumb li a {
  color: white;
}
.navbar .nav > li > .dropdown-menu {
  top: 30px;
}
.scrollspy .nav {
  background: #415B76;
  margin-top: 20px;
}
@media (max-width: 979px) {
  .scrollspy .nav {
    width: 15%;
    max-width: 120px;
  }
}
@media (min-width: 980px) {
  .scrollspy .nav (max-width: 1199) {
    width: 13%;
    min-width: 120px;
  }
}
@media (min-width: 980px) and (min-width: 1200px) {
  .scrollspy .nav {
    width: 13%;
    max-width: 180px;
  }
}
.scrollspy .nav li {
  padding: 0;
  margin: 0;
  border-bottom: 1px solid #2f4255;
  border-top: 1px solid #537497;
}
.scrollspy .nav li a {
  color: white;
  text-indent: -20px;
  padding-left: 30px;
}
.scrollspy .nav li a:hover {
  background: #F4D313;
  color: black;
  transition: background, color 0.5s ease;
}
.scrollspy .nav li.active {
  background: #E74C3C;
}
.scrollspy .nav li:first-child {
  border-top: none;
}
.scrollspy .nav li:last-child {
  border-bottom: none;
}
.breadcrumb > li:after {
  display: inline-block;
  content: "";
  padding: 0 5px;
  color: #CCC;
}
.jumbotron h1 {
  font-size: 50px;
  color: #F5F5DC;
}
.jumbotron {
  padding: 30px;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 200;
  line-height: 2.14285714;
  color: inherit;
  background-color: #303030;
}
.jumbotron p {
  color: #F5F5DC;
  font-size: 75%;
}
.table-responsive {
  width: 100%;
  margin-bottom: 15px;
  overflow-y: hidden;
  overflow-x: scroll;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  -webkit-overflow-scrolling: touch;
}
.center {
  text-align: center;
  margin: auto;
  display: block;
  width: 100%;
  height: 100%;
}
.btn {
  margin: 1% 0;
}
.selected {
  font-weight: bold;
  color: #E74C3C !important;
}
.range-wrap {
  overflow: hidden;
}
.glyphicon-user:before {
  display: block;
}
form.addtoplaylist,
form.removefromplaylist {
  display: inline;
}
.input-group-addon:first-child {
  border-right: 1px solid #CCC;
}
.input-group input:last-child,
.input-group-addon:last-child,
.input-group-btn:first-child > .btn:not(:first-child),
.input-group-btn:last-child > .btn,
.input-group-btn:last-child > .dropdown-toggle {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
.dl-horizontal dt {
  float: right;
}
.bgvideo {
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}
.fb_iframe_widget,
.fb_iframe_widget span,
.fb_iframe_widget span iframe[style] {
  min-width: 100% !important;
  width: 100% !important;
}
.share-buttons {
  opacity: .6;
  transition-duration: 1s;
}
.share-buttons:hover {
  opacity: 1;
  transition-duration: 1s;
}
.play {
  -webkit-animation: box 10s infinite alternate;
  -moz-animation: box 10s infinite alternate;
  -o-animation: box 10s infinite alternate;
  animation: box 10s infinite alternate;
}
@-webkit-keyframes box {
  0% {
    background-color: #373600;
  }
  25% {
    background-color: #37002D;
  }
  50% {
    background-color: #370001;
  }
  75% {
    background-color: #000337;
  }
  100% {
    background-color: #073700;
  }
}
@-moz-keyframes box {
  0% {
    background-color: #373600;
  }
  25% {
    background-color: #37002D;
  }
  50% {
    background-color: #370001;
  }
  75% {
    background-color: #000337;
  }
  100% {
    background-color: #073700;
  }
}
@-o-keyframes box {
  0% {
    background-color: #373600;
  }
  25% {
    background-color: #37002D;
  }
  50% {
    background-color: #370001;
  }
  75% {
    background-color: #000337;
  }
  100% {
    background-color: #073700;
  }
}
@keyframes box {
  0% {
    background-color: #373600;
  }
  25% {
    background-color: #37002D;
  }
  50% {
    background-color: #370001;
  }
  75% {
    background-color: #000337;
  }
  100% {
    background-color: #073700;
  }
}
